import { Injectable } from '@angular/core';

@Injectable()
export class LoaderService {
    constructor() {
        
    }

    showLoader() {
      //$('.overlay').fadeIn(300);
      document.getElementById("loadingImage").style.display = "block";
    };

   hideLoader() {
      //$('.overlay').fadeOut(300);
      document.getElementById("loadingImage").style.display = "none";
    };
}


