import { NotificationService } from './../../providers/service/notification.service';
import { FirebseService } from './../../providers/service/firebase.service';
import { Pet, Notification } from './../../providers/utils/schema';
import { PetManagement } from './../../providers/service/petmanagement.service';
import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { FirebaseListObservable, FirebaseObjectObservable } from "angularfire2/database"
import * as Rx from "rxjs/Rx";
import {TooltipPosition} from '@angular/material';
import { LoaderService } from './../../providers/service/loader.service';

@Component({
	selector: 'home',
	templateUrl : './home.html',
	styleUrls : ['./home.css']
})

export class HomePage {
	recomendedPets: Rx.Observable<Pet> = null;
	selectedPet: Pet = new Pet();
	myPet: Pet;
	pets:Pet[] = [];
	selectedIndex:number = 0;
	owner: FirebaseObjectObservable<any>;

	position: TooltipPosition = 'below';
    notificationTip: string = 'Notifications';
    profileTip: string = 'Profile';
    logoutTip: string = 'Logout';
    nextTip : string = 'Next';
    showIntrest : string = 'Show Intrest';
    disabled = false;
    showDelay = 0;
    hideDelay = 200;
	selectedImageUrl:string = null;

	constructor(public router: Router, 
				private firebaseService: FirebseService, 
				private petManagement: PetManagement, 
				private loader: LoaderService,
				private notificationService: NotificationService) {

	}

	ngOnInit() {
		this.loader.showLoader();
		this.petManagement.getMyPet()
		.then((pet: Pet) => {
			this.myPet = pet;
			this.loader.hideLoader();
			let gender = pet.gender,
				oppositeGender = gender == "Male" ? "Female" : "Male";
			this.recomendedPets = this.petManagement.getAllPetsOfTypeAndOppositeGender(pet.type, pet.gender);

			this.recomendedPets.subscribe((pets: any) => {
				pets = pets || [];
				this.selectedPet = pets[0];
				this.pets = pets;
				if(this.selectedPet){
					this.loadOwnerDetails(this.selectedPet);
				}
			});
			
		})
		.catch((error) => {
			this.loader.hideLoader();
			console.log(error);
		});
	}
	selectPet(pet: Pet){
		this.selectedPet = pet;
		this.loadOwnerDetails(this.selectedPet);
	}
	selectPetImage(imageUrl: string) {
		this.selectedImageUrl = imageUrl;
	}
	nextPet(){
		if(this.pets.length){
			if(this.selectedIndex < (this.pets.length -1)){
				this.selectedIndex++;
				this.selectPet(this.pets[this.selectedIndex]);
				this.selectedImageUrl = null;
			}
		}
	}
	addNotification() {
		let pet = this.selectedPet;
		let loggedInUser = this.firebaseService.loggedInUser;
		if(loggedInUser.uid && this.myPet && pet){
			let notification = new Notification();
			notification.subject = pet.name;
			notification.message = pet.description || "";
			notification.receiver = pet.owner;
			notification.sender = loggedInUser.uid;
			notification.senderPetKey = this.myPet.$key;
			notification.receiverPetKey = pet.$key;
			
			this.notificationService.createNotification(notification);
			this.onPetLikeButtonClick(false);
		}
	}

	onPetLikeButtonClick(allowToggle: boolean = false){
		if(this.selectedPet){
			this.petManagement.onPetLikeButtonClick(this.selectedPet, allowToggle);
		}
		
	}

	loadOwnerDetails(pet: Pet){
		this.owner = this.firebaseService.getUserProfileDataObservable(pet.owner);
	}

	getPetImageUrl (pet: Pet){
		if(!pet){
			//console.log(pet);
			return null;
		}
		let images = pet.images || [],
			image: any = images[0] || {};
		return image.url;

	}

	gotoProfile() {
		this.router.navigateByUrl('ownerprofile');
	}

	gotoNotification() {
		this.router.navigateByUrl('notification');
	}

	logout() {
		this.firebaseService.logout();
	}
}


