import { Notification, User } from './../utils/schema';
import { AngularFireDatabase } from 'angularfire2/database';
import { FirebseService } from './firebase.service';
import { Injectable } from '@angular/core';
import * as Rx from "rxjs/Rx";

@Injectable()
export class NotificationService {
    constructor(private firebaseService: FirebseService, private dbProvider: AngularFireDatabase){

    }

    getMyNotifications(){
        let loggedinUser = this.firebaseService.loggedInUser;
        if(!loggedinUser){
            return
        }
        return this.dbProvider.list("/notifications/" + loggedinUser.uid);
    }
    getUnreadNotifications() {

    }
    createNotification(notification: Notification){
        this.addReceiverNotification(notification);
        //this.addSenderNotification(notification);
    }
    addSenderNotification(notification: Notification){
        let clonedNotification: Notification = Object.assign({}, notification);
        clonedNotification.isRead = true;
        clonedNotification.initiatedByMe = true;

        this.dbProvider.list("/notifications/" + notification.sender).push(clonedNotification);
    }
    addReceiverNotification(notification: Notification){
        notification.sender = this.firebaseService.loggedInUser.uid;
        notification.isRead = false;
        notification.initiatedByMe = false;
        notification.createdOn = new Date();
        this.dbProvider.list("/notifications/" + notification.receiver).push(notification);
    }
    updateNotification(notification: Notification){
       return this.dbProvider.list("/notifications/" + notification.receiver).update(notification.$key, notification);
    }
    updateNotificationAsReadByUser(notification: Notification){
        if(notification.isRead !== false){
            return new Promise((resolve, reject) => resolve(notification));
        }
        notification.isRead = true;
        return this.updateNotification(notification);
    }

    getUserNotifications(){
        return Rx.Observable.create((observer) => {
            let authStateSubscription: Rx.Subscription = this.firebaseService.authState.subscribe((user: User) => {
                authStateSubscription.unsubscribe();
                if(user){
                    observer.next(this.dbProvider.list("/notifications/" + user.uid));
                }
                else{
                    observer.next(null);
                }
            })
        });
    }
    

}


