import { LoaderService } from './loader.service';
import { Router } from '@angular/router';
import { Injectable } from "@angular/core";
import * as firebase from "firebase";
import { AngularFireAuth } from "angularfire2/auth";
import { AngularFireDatabase } from "angularfire2/database"
import { User } from './../utils/schema';
import * as Rx from "rxjs/Rx"

@Injectable()
export class FirebseService {

    authState:Rx.Subject<User> = null;
    isAuthTrackingEnabled: boolean = true;
    loggedInUser: User = null;

    constructor(private authProvider: AngularFireAuth, 
                private dbProvider: AngularFireDatabase,
                private loader: LoaderService,
                private router: Router){
        this.trackAuthState();
    }

    disableAuthTracking() {
        this.isAuthTrackingEnabled = false;
    }

    enableAuthTracking() {
        this.isAuthTrackingEnabled = true;
    }
    private trackAuthState() {
        this.authState = Rx.Observable.create((observer) => {
            this.authProvider.authState.subscribe((user) => {
                if(user){
                    let providers= user.providerData || [],
                        provider:any = providers[0] || {};
                    if(provider.providerId == "password"){
                        // login from Userid and Password
                        this.getUserProfileData(user.uid)
                        .then((userInstance: User) => {
                            this.loggedInUser = userInstance;
                            observer.next(userInstance);
                        })
                        .catch((error) => {
                            this.loggedInUser = null;  
                            observer.error(error);
                        });
                    }
                    else {
                        // login from Google or Facebook
                        let userInstance = new User();
                        userInstance.name = user.displayName;
                        userInstance.email = user.email;
                        userInstance.uid = user.uid;
                        userInstance.profileImage = user.photoURL;
                        this.loggedInUser = userInstance;
                        observer.next(userInstance);
                    }
                }
                else{
                    this.loggedInUser = null;
                    observer.next(null);
                }
            });
        });
    }

    registerUser(user: User){
        return new Promise((resolve, reject) => {
            this.authProvider.auth.createUserWithEmailAndPassword(user.email, user.password)
            .then((data) => {
                user.uid = data.uid;
                //Adding user profile and a map for setting email against mobile.
                Promise.all([this.saveUserProfile(user), this.saveUserMobile(user)])
                this.saveUserProfile(user)
                .then(() => {
                    resolve(user);
                })
                .catch((error) => {
                    reject(error);
                })
            })
            .catch((error) => {
                reject(error);
            });
        });
    }

    saveUserProfile(user: User){
        return this.dbProvider.object("users/profile/" + user.uid).update(user);
    }
    saveUserMobile(user: User) {
        return this.dbProvider.object("users/mobile/" + user.mobile).set(user.email);
    }

    getUserProfileData(uid: string){
        return new Promise((resolve, reject) => {
            this.dbProvider.object("users/profile/" + uid).subscribe((user: User) => {
                if(user){
                    delete user.password;
                    resolve(user);
                }
                else{
                    reject("No user profile data available");
                }
            });
        });
    }
    getUserProfileDataObservable(uid: string) {
        return this.dbProvider.object("users/profile/" + uid);
    }

    loginWithGoogle() {
        let provider = new firebase.auth.GoogleAuthProvider();
        provider.addScope("https://www.googleapis.com/auth/plus.login");
        return this.authProvider.auth.signInWithPopup(provider);
    }

    loginWithFacebook() {
        let provider = new firebase.auth.FacebookAuthProvider();
        return this.authProvider.auth.signInWithPopup(provider);
    }

    loginWithEmail(email: string, password:string) {
        return this.authProvider.auth.signInWithEmailAndPassword(email, password);
    }

    loginWithMobile(mobile: string, password:string) {
        let subscription: Rx.Subscription = null;
        return new Promise((resolve, reject) => {
            subscription = this.dbProvider.object("users/mobile/" + mobile)
            .subscribe((snapshot: any) => {
                subscription.unsubscribe();
                if(snapshot.$exists()){
                    this.loginWithEmail(snapshot.$value, password)
                    .then((data) => {
                        resolve(data);
                    })
                    .catch((error) => {
                        reject(null);
                    })
                }
                else{
                    reject(null);
                }
            });
        });
        
        //return this.loginWithEmail(email, password);
    }

    logout (){
        this.loader.showLoader();
        this.authProvider.auth.signOut()
        .then(() => {
            this.loader.hideLoader();
            this.router.navigate(["signin"]);
        })
        .catch(() => {
            this.loader.hideLoader();
        });
    }
}


