import { User } from './../../providers/utils/schema';
import { FirebseService } from './../../providers/service/firebase.service';
import { Router } from '@angular/router';
import { Component } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import {TooltipPosition} from '@angular/material';


@Component({
	selector: 'owner-profile',
	templateUrl: './ownerprofile.html',
	styleUrls: ['./ownerprofile.css']
})
export class OwnerProfilePage {

	fitListHeight = '400px';
	ratio = '4:1';
	user: User = null;
	position: TooltipPosition = 'below';
	  notificationTip: string = 'Notifications';
	  profileTip: string = 'Profile';
	  logoutTip: string = 'Logout';
	  nextTip : string = 'Pet Profile';
	  showIntrest : string = 'Home';
	  disabled = false;
	  showDelay = 0;
	  hideDelay = 200;

	constructor(public router: Router, private firebaseService: FirebseService) {

	}

	ngOnInit(){
		this.user = this.firebaseService.loggedInUser;
	}

	petProfile() {
		this.router.navigateByUrl('petprofile');
	}

	onProfile() {
		this.router.navigateByUrl('ownerprofile');
	}

	onNotifications() {
		this.router.navigateByUrl('notification');
	}

	onHome() {
		this.router.navigateByUrl('home');
	}

	logout() {
		this.firebaseService.logout();
	}

}



