import { Pet } from './../providers/utils/schema';
import { PetManagement } from './../providers/service/petmanagement.service';
import { LoaderService } from './../providers/service/loader.service';
import { Router } from '@angular/router';
import { FirebseService } from './../providers/service/firebase.service';
import { Component, OnDestroy } from '@angular/core';
import * as Rx from "rxjs/Rx";
import { LoginService } from './../providers/service/login.service';

@Component({
	selector: 'app-root',
	template: `<router-outlet></router-outlet>`
})
export class AppComponent implements OnDestroy{
	title = 'app works!';
	authSubscription: Rx.Subscription = null;

	constructor(private firebaseService: FirebseService, 
				private router: Router, 
				private loginService: LoginService,
				private loader: LoaderService,
				private petManagement: PetManagement) {
		this.checkLoginStatus();
	}

	checkLoginStatus (){
		this.loader.showLoader();
		this.authSubscription = this.firebaseService.authState.subscribe((user) => {
			this.loader.hideLoader();
			console.log(user)
			if(user){
				this.loginService.setLogin(true);
				this.petManagement.getMyPet().then((pet: Pet) => {
					if(pet){
						this.router.navigate(["/home"]);
					}
					else{
						this.router.navigate(["/petdetails"]);
					}
					this.loader.hideLoader();
				})
				.catch((error) => {
					this.loader.hideLoader();
				})
			}
			else{
				this.loader.hideLoader();
				this.loginService.setLogin(false);
				//this.router.navigate(["/signin"]);
			}
			
		})
	}

	ngOnDestroy(){
		if(this.authSubscription){
			this.authSubscription.unsubscribe();
		}
	}
}



