import { Injectable } from '@angular/core';
import { Pet, User } from './../utils/schema';
import { AngularFireDatabase } from 'angularfire2/database';
import { FirebseService } from './firebase.service';
import * as firebase from "firebase";
import * as Rx from "rxjs/Rx";

@Injectable()
export class PetManagement {
    myPet: Pet = null;
    constructor(private firebaseService: FirebseService, 
                private dbProvider: AngularFireDatabase){
    }

    getAllPets(){
        return this.dbProvider.list("/pets");
    }
    getAllPetsOfTypeAndOppositeGender(type, gender){
        
        return this.dbProvider.list("/pets", {
            query : {
                orderByChild : "gender",
                equalTo : gender
            }
        })
        .filter((pets: Pet[]) => {
            return true;
            //return pet.type == type;
        });
    }
    getMyPets(){
        /*let loggedInUser = this.firebaseService.loggedInUser;
        debugger;
        if(!loggedInUser){
            return;
        }*/
        return Rx.Observable.create((observer) => {
            let authStateSubscription: Rx.Subscription = this.firebaseService.authState.subscribe((user: User) => {
                authStateSubscription.unsubscribe();
                if(user){
                    //observer.next(user);
                    let listSubscription: Rx.Subscription = this.dbProvider.list("/pets", {
                        query : {
                            orderByChild : "owner",
                            equalTo : user.uid
                        }
                    }).subscribe((pets: Pet[]) => {
                        //listSubscription.unsubscribe();
                        observer.next(pets);
                    })
                }
                else{
                    observer.next(null);
                }
            })
        })
        /*return this.dbProvider.list("/pets", {
            query : {
                orderByChild : "owner",
                equalTo : this.firebaseService.loggedInUser.uid
            }
        });*/
    }

    getMyPet() {
        if(this.myPet){
            return new Promise((resolve, reject) => resolve(this.myPet))
        }
        else{
            return new Promise((resolve, reject) => {
                let subscription: Rx.Subscription = this.getMyPets().subscribe((pets: Pet[]) => {
                    if(pets && pets.length > 0){
                        this.myPet = pets[0];
                        resolve(this.myPet);
                    }
                    else{
                        reject(null);
                    }
                });
            });
            
        }
    }

    getPetObservable(petKey: string){
        return this.dbProvider.object("/pets/" + petKey);
    }


    addNewPet(pet: Pet) {
        if(!pet.images){
            pet.images = [];
        }
        pet.likedCount = 0;
        pet.owner = this.firebaseService.loggedInUser.uid;
        return this.dbProvider.list("/pets").push(pet);
    }
    uploadImage(path:string, content: string, contentType?:string){
        return firebase.storage().ref().child(path).putString(content, contentType);
    }

    updatePetDetails(pet: Pet){
        return this.dbProvider.list("/pets").update(pet.$key, pet);
    }

    onPetLikeButtonClick(pet: Pet, allowToggle: boolean = false){
        let loggenInUser = this.firebaseService.loggedInUser;
        pet.liked = pet.liked || [];
        pet.likedCount = pet.likedCount || 0;
        let userIndex = pet.liked.indexOf(loggenInUser.uid);
        if(userIndex == -1){
            pet.likedCount++;
            pet.liked.push(loggenInUser.uid);
        }
        else if(allowToggle){
            pet.likedCount--;
            pet.liked.splice(userIndex, 1);
        }
        return this.updatePetDetails(pet);
    }
    

}


