import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SigninPage } from './../pages/signin/signin';
import { SignupPage } from '../pages/signup/signup';
import { PetDetailsPage } from '../pages/petdetail/pet-details';
import { HomePage } from '../pages/home/home';
import { OwnerProfilePage } from '../pages/ownerprofile/ownerprofile';
import { PetProfilePage } from '../pages/petprofile/petprofile';
import { NotificationPage } from '../pages/notification/notification';
import { WelcomePage } from '../pages/welcome/welcome';
import { LoginRouteGuard } from '../providers/service/auth-guard-service';

// declaring routes
export const routes: Routes = [
    {
        path: '', redirectTo: '/whoopee', pathMatch: 'full'
    },
    {
        path: 'signin', component: SigninPage
    },
    {
        path: 'signup', component: SignupPage
    },
    {
        path: 'petdetails', component: PetDetailsPage, canActivate: [LoginRouteGuard]
    },
    {
        path: 'ownerprofile', component: OwnerProfilePage, canActivate: [LoginRouteGuard]
    },
    {
        path: 'petprofile', component: PetProfilePage, canActivate: [LoginRouteGuard]
    },
    {
        path: 'notification', component: NotificationPage, canActivate: [LoginRouteGuard]
    },
    {
        path: 'home', component: HomePage, canActivate: [LoginRouteGuard]
    },
    {
        path: 'whoopee', component: WelcomePage
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: false })],
    exports: [RouterModule]
})

export class AppRoutingModule { }


