import { NotificationService } from './../providers/service/notification.service';
import { PetManagement } from './../providers/service/petmanagement.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { AngularFireModule } from "angularfire2";
import { AngularFireAuthModule } from "angularfire2/auth";
import { AngularFireDatabaseModule } from "angularfire2/database";

import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MaterialModule} from '@angular/material';
import {FileUploadModule} from 'ng2-file-upload'; 

import { AppComponent } from './app.component';

//importing Routing module
import { AppRoutingModule } from './app.routing';

//importing components
import { SigninPage } from '../pages/signin/signin';
import { SignupPage } from '../pages/signup/signup';
import { PetDetailsPage } from '../pages/petdetail/pet-details';
import { HomePage } from '../pages/home/home';
import { OwnerProfilePage } from '../pages/ownerprofile/ownerprofile';
import { PetProfilePage } from '../pages/petprofile/petprofile';
import { NotificationPage } from '../pages/notification/notification';
import { WelcomePage } from '../pages/welcome/welcome';

//importing services
import { FirebseService } from './../providers/service/firebase.service';

//importing configs
import { firebaseConfig } from './config/firebase.config';
import { whoopeeService } from './../providers/service/whoopee.service';
import { LoaderService } from './../providers/service/loader.service';
import { LoginService } from './../providers/service/login.service';
import { LoginRouteGuard } from './../providers/service/auth-guard-service';

@NgModule({
  declarations: [
    AppComponent,
    SigninPage,
    SignupPage,
    PetDetailsPage,
    HomePage,
    OwnerProfilePage,
    PetProfilePage,
    NotificationPage,
    WelcomePage
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    MaterialModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    FileUploadModule
  ],
  providers: [FirebseService,whoopeeService, PetManagement, LoaderService, LoginService, LoginRouteGuard, NotificationService],
  bootstrap: [AppComponent]
})
export class AppModule { }



