import { NotificationService } from './../../providers/service/notification.service';
import { LoaderService } from './../../providers/service/loader.service';
import { PetManagement } from './../../providers/service/petmanagement.service';
import { FirebseService } from './../../providers/service/firebase.service';
import { Component } from '@angular/core';
import { Router } from '@angular/router';
import {TooltipPosition} from '@angular/material';
import { FirebaseListObservable } from "angularfire2/database"

@Component({
	selector: 'notification',
  	templateUrl: './notification.html',
  	styleUrls: ['./notification.css']
})

export class NotificationPage {
	position: TooltipPosition = 'below';
	notificationTip: string = 'Notifications';
	profileTip: string = 'Profile';
	logoutTip: string = 'Logout';
	nextTip : string = 'Next';
	showIntrest : string = 'Home';
	disabled = false;
	showDelay = 0;
	hideDelay = 200;

	notifications: FirebaseListObservable<any>;

	constructor(public router: Router, 
              	private firebaseService: FirebseService, 
				private petManagement: PetManagement, 
				private loader: LoaderService,
				private notificationService: NotificationService) {

	}

	ngOnInit(){
		this.notificationService.getUserNotifications()
		.subscribe((notifications: FirebaseListObservable<any>) => {
			this.notifications = notifications;
		})
	}
	getUserProfileData(uid: string){
		return this.firebaseService.getUserProfileDataObservable(uid);
	}

	getUserPet(petKey: string){
		return this.petManagement.getPetObservable(petKey);
	}


   messages: any[] = [
    {
      from: 'Nancy',
      subject: 'Brunch?',
      message: 'Did you want to go on Sunday? I was thinking that might work.',
      image: 'https://angular.io/resources/images/bios/julie-ralph.jpg',
      mobile:'9096767676'
    },
    {
      from: 'Mary',
      subject: 'Summer BBQ',
      message: 'Wish I could come, but I have some prior obligations.',
      image: 'https://angular.io/resources/images/bios/juleskremer.jpg',
      mobile:'9065688676'
    },
    {
      from: 'Bobby',
      subject: 'Oui oui',
      message: 'Do you have Paris reservations for the 15th? I just booked!',
      image: 'https://angular.io/resources/images/bios/jelbourn.jpg',
      mobile:'9075445336'
    }
  ];

    onProfile(){
        this.router.navigateByUrl('ownerprofile');
    }
  
    onHome(){
        this.router.navigateByUrl('home');
    }

    onLogout() {
        this.firebaseService.logout();
    }
}


