import { FirebseService } from './../../providers/service/firebase.service';
import { PetManagement } from './../../providers/service/petmanagement.service';
import { Pet, PetImage } from './../../providers/utils/schema';
import { Component } from '@angular/core';
import { FileUploader } from 'ng2-file-upload';
import { whoopeeService } from './../../providers/service/whoopee.service';
import { Router } from '@angular/router';
import {TooltipPosition} from '@angular/material';
import { LoaderService } from './../../providers/service/loader.service';

@Component({
	selector: 'pet-details',
  	templateUrl: './pet-details.html',
  	styleUrls: ['./pet-details.css']
})

export class PetDetailsPage {
	public sliderValue: number = 12.5;
	public petDetails: any = {};
	public images: any = [];
	public hasBaseDropZoneOver:boolean = false;
  	public hasAnotherDropZoneOver:boolean = false;
  	public uploader : FileUploader = new FileUploader({});
	public ths: any;
	public petTypes: any = [];

	position: TooltipPosition = 'below';
	  
	  nextTip : string = 'Next';
	  disabled = false;
	  showDelay = 0;
	  hideDelay = 200;

	constructor(public whoopeeService: whoopeeService, 
				public router: Router, 
				private petManagement: PetManagement,
				private firebaseService: FirebseService, private loader: LoaderService) {

		this.ths = this;
	}

	ngOnInit(){
		this.petTypes = this.whoopeeService.getPetTypes();
	}

	next(){
		this.sliderValue = this.sliderValue + 12.5;
	}

	gender(value){
		this.petDetails.gender = value;
	}

	vaccination(value){
		this.petDetails.vaccination = value;
	}

	continue(){
		this.petDetails.images = [];
		this.petDetails.images = this.images;
		let pet = new Pet();
		pet.name = this.petDetails.petName;
		pet.age = this.petDetails.petAge;
		pet.breed = this.petDetails.petBreed;
		pet.gender = this.petDetails.gender;
		pet.vaccination = this.petDetails.vaccination;
		pet.type = this.petDetails.petType;
		pet.description = this.petDetails.petDescription;
		pet.images = this.petDetails.images || [];

		this.loader.showLoader();
		this.petManagement.addNewPet(pet)
		.then((data)=> {
			this.loader.hideLoader();
			this.router.navigateByUrl('petprofile');
		})
		.catch((error) => {
			this.loader.hideLoader();
		})
		this.whoopeeService.setPetDetails(this.petDetails);
		
	}

	public fileOverBase(e:any):void {
	   this.hasBaseDropZoneOver = e;
	}

	public fileOverAnother(e:any):void {
    	this.hasAnotherDropZoneOver = e;
  	}

  	public onFileDrop(files:File[], ths) {
		this.images = [];
		let promises = [];
	    for( var i = 0; i < files.length; i++) {
			promises.push (this.readFile(files[i], i));
	    }
	    this.loader.showLoader();
		Promise.all(promises)
		.then((images) => {
			this.loader.hideLoader();
			console.log(images);
		})
		.catch((error) => {
			this.loader.hideLoader();
			console.log(error);
		})
	}
	readFile(file: File, index: number) {
		let loggedInUser = this.firebaseService.loggedInUser;
		return new Promise((resolve, reject) => {
			var reader = new FileReader();
			this.loader.showLoader();
			reader.onload = () => {
				var dataURL = reader.result;
				this.petManagement.uploadImage(`/images/${loggedInUser.uid}/${index}`, dataURL, "data_url")
				.then((snapshot) => {
					this.loader.hideLoader();
					let image = new PetImage();
					image.createdOn = new Date();
					image.liked = [];
					image.likedCount = 0;
					image.title = file.name;
					image.url = snapshot.downloadURL;
					this.images.push(image);
					resolve(image);
				})
				.catch((error) => {
					this.loader.hideLoader();
					reject(error)
				});
			};
			reader.readAsDataURL(file);
		});
		
	}
}


