import { Injectable } from '@angular/core';

@Injectable()
export class LoginService {
	public isUserLoggedIn : boolean = false;

    constructor() {
        
    }

    isLoggedIn() {
      return this.isUserLoggedIn;
    }

    setLogin(isloggedIn) {
      this.isUserLoggedIn = isloggedIn;
    }
}


