import { FirebseService } from './../../providers/service/firebase.service';
import { Pet, PetImage } from './../../providers/utils/schema';
import { PetManagement } from './../../providers/service/petmanagement.service';
import { Router } from '@angular/router';
import { Component } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import {TooltipPosition} from '@angular/material';
import { LoaderService } from './../../providers/service/loader.service';

@Component({
	selector: 'pet-profile',
	templateUrl: './petprofile.html',
	styleUrls: ['./petprofile.css']
})
export class PetProfilePage {



	fitListHeight = '400px';
	ratio = '4:1';

	selectedGender: string;
	selectedVaccination: string;
	isImage: boolean = false;
	myPet: Pet = new Pet();

	position: TooltipPosition = 'below';
	  notificationTip: string = 'Notifications';
	  profileTip: string = 'Profile';
	  logoutTip: string = 'Logout';
	  nextTip : string = 'Pet Profile';
	  showIntrest : string = 'Home';
	  disabled = false;
	  showDelay = 0;
	  hideDelay = 200;

	constructor(public router: Router, 
				private petManagement: PetManagement, 
				private firebaseService: FirebseService, 
				private loader: LoaderService) {

	}

	ngOnInit() {
		// this.petManagement.getMyPets().subscribe((pets) => {
		// 	if(pets && pets.length > 0){
		// 		this.myPet = pets[0];
		// 	}
		// });
		this.getMyPet();
	}
	getMyPet() {
		this.loader.showLoader();
		this.petManagement.getMyPet()
		.then((pet: Pet) => {
			this.loader.hideLoader();
			this.myPet = pet;
		})
		.catch((error) => {
			this.loader.hideLoader();
		});
	}


	genders = ['Male', 'Female'];

	vaccinations = ['Yes', 'No'];

	dogs: Object[] = [
		{ name: 'Porter', human: 'Kara' },
		{ name: 'Mal', human: 'Jeremy' },
		{ name: 'Koby', human: 'Igor' },
		{ name: 'Razzle', human: 'Ward' },
		{ name: 'Molly', human: 'Rob' },
		{ name: 'Husi', human: 'Matias' },
	];

	petImages() {
		this.isImage = true;
	}
	onBack() {
		this.isImage = false;
	}

	onProfile() {
		this.router.navigateByUrl('ownerprofile');
	}

	onNotifications() {
		this.router.navigateByUrl('notification');
	}
	onHome() {
		this.router.navigateByUrl('home');
	}

	removePetImage(image: PetImage){
		if(this.myPet){
			let images = this.myPet.images || [];
			let clonedImages = images.slice(0);
			let index = images.indexOf(image);
			if(index > -1){
				images.splice(index, 1);
				this.petManagement.updatePetDetails(this.myPet)
				.then(() => {
					
				})
				.catch((error) => {
					this.myPet.images = clonedImages;
					console.log(error);
				});
			}
		}
	}
	logout(){
		this.firebaseService.logout();
	}


}






