import { User } from './../../providers/utils/schema';
import { Router } from '@angular/router';
import { FirebseService } from './../../providers/service/firebase.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder , Validators } from '@angular/forms';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.html',
  styleUrls: ['./signup.css']
})
export class SignupPage implements OnInit {

  	signUpForm : FormGroup;

  	constructor(public formBuilder : FormBuilder, 
				private firebaseService: FirebseService,
				private router: Router) { 

		this.signUpForm = formBuilder.group({
			'NAME':[null,Validators.required],
			'EMAIL': [null, Validators.compose([Validators.required, Validators.pattern(/^\b[A-Z0-9._%-]+@[A-Z0-9.-]+\.[A-Z]{2,4}\b$/i)])],
			'MOBILE':[null,Validators.compose([Validators.required, Validators.pattern(/^[7-9][0-9]{9}$/)])],
			'PASSWORD':[null,Validators.required]
		})
	}

  	ngOnInit() {
  	
	}

	signup(){
		if(this.signUpForm.valid){
			let formData = this.signUpForm.value;
			let user = new User();
			user.email = formData.EMAIL;
			user.mobile = formData.MOBILE;
			user.name = formData.NAME;
			user.password = formData.PASSWORD;

			this.firebaseService.registerUser(user)
			.then((o) => {
				this.router.navigate(["/petdetails"]);
			})
			.catch((error) => {
				console.log(error);
			});
		}
	}

	signin() {
		this.router.navigate(["/signin"]);
	}

}



