
export class User {
    name: string;
    email?: string;
    password?: string;
    mobile?: string;
    uid?: string;
    profileImage?: string;
}
export class Pet {
    owner: string;
    name: string;
    age: number;
    breed: string;
    type: string;
    gender: string;
    description: string;
    vaccination: boolean;
    images: Array<PetImage>;
    liked: Array<string>;
    likedCount: number;
    $key?: string;
}

export class PetImage {
    url : string;
    createdOn : Date;
    title : string;
    descrition : string;
    liked: Array<string>;
    likedCount: number;
    $key?: string;
}

export class Notification {
    sender: string;
    receiver: string;
    subject: string;
    message: string;
    initiatedByMe: boolean;
    isRead ?: boolean;
    createdOn ?: Date;
    $key?: string;
    senderPetKey: string;
    receiverPetKey: string;
    petKey: string;
}


