import { Router } from '@angular/router';
import { FirebseService } from './../../providers/service/firebase.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { LoaderService } from './../../providers/service/loader.service';

@Component({
	selector: 'app-signin',
	templateUrl: './signin.html',
	styleUrls: ['./signin.css']
})
export class SigninPage implements OnInit {

	signInForm: FormGroup;

	constructor(public formBuilder: FormBuilder, private firebaseService: FirebseService, private router: Router, private loader: LoaderService) {
		this.signInForm = formBuilder.group({
			'EMAIL': [null, Validators.compose([Validators.required, Validators.pattern(/^\b[A-Z0-9._%-]+@[A-Z0-9.-]+\.[A-Z]{2,4}\b$/i)])],
			'MOBILE_NUM': [null, Validators.compose([Validators.required, Validators.pattern(/^[7-9][0-9]{9}$/)])],
			'PASSWORD':[null,Validators.required]
		})
	}

	ngOnInit() {

	}
	signinWithGoogle() {
		this.loader.showLoader();
		this.firebaseService.loginWithGoogle()
		.then((user) => {
			this.loader.hideLoader();
			console.log(user);
		})
		.catch((error) => {
			this.loader.hideLoader();
			console.log(error);
		});
	}

	signinWithFacebook() {
		this.loader.showLoader();
		this.firebaseService.loginWithFacebook()
		.then((user) => {
			this.loader.hideLoader();
			console.log(user);
		})
		.catch((error) => {
			this.loader.hideLoader();
			console.log(error);
		});
	}

	signin(){
		//this.router.navigate(["/petdetails"]);
		let formData = this.signInForm.value;
		if(formData.MOBILE_NUM){
			this.signinWithMobile();
		}
		else{
			this.signinWithEmail();
		}
	}

	signinWithEmail(){
		let formData = this.signInForm.value,
			email = formData.EMAIL,
			password = formData.PASSWORD;
		this.firebaseService.loginWithEmail(email, password)
		.then(() => {

		})
		.catch((error) => {

		});
	}
	signinWithMobile(){
		this.loader.showLoader();
		let formData = this.signInForm.value,
			mobile = formData.MOBILE_NUM,
			password = formData.PASSWORD;
		this.firebaseService.loginWithMobile(mobile, password)
		.then((data: any) => {
			this.loader.hideLoader();
			this.router.navigate(["/petdetails"]);
		})
		.catch((error) => {
			debugger;
			this.loader.hideLoader();
		});
	}

	signup(){
		this.router.navigate(["/signup"]);
	}

}




